<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="materialCodeOrName">
          <el-input v-model="query.materialCodeOrName" filterable clearable placeholder="物料编码或名称" style="width: 178px;" class="filter-item" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-form-item prop="assessType">
          <el-select v-model="query.assessType" filterable clearable placeholder="评估类型" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in dict['wms_material_assess_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-download"
          @click="handleDownloadTem"
        >下载模板</el-button>
        <form-upload-file
          ref="uploadCom"
          class="filter-item fr"
          accept=".xls,.xlsx"
          url="/basic/wmsMaterial/importData"
          btn-content="批量导入"
          btn-icon="el-icon-upload"
          :limit="99999"
          :show-file-list="false"
          @uploadFileSuccess="handleUploadSuccess"
          @uploadFileError="handleUploadFileError"
        >
          <template v-slot:tip />
        </form-upload-file>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="180"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <edit-dialog v-model="dialogVisible" :dialog-form="dialogForm" :edit-type="dialogType" :title="dialogTitle" @submit="init" />
  </div>
</template>
<script>
import { getListApi, getDeleteApi } from '@/apis/modules/base/baseMaterialInformation'
import paginationPage from '@/mixins/paginationPage'
import { formUploadFilesMixins } from '@/mixins/upload'
import EditDialog from './Dialog'
export default {
  name: 'WMSBaseMaterialInformation',
  components: {
    EditDialog
  },
  dicts: ['wms_material_assess_type'],
  mixins: [paginationPage, formUploadFilesMixins],
  data() {
    return {
      query: {
        materialCodeOrName: '',
        assessType: ''
      },
      dialogForm: {},
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      dialogType: 'add',
      dialogVisible: false,
      columnList: [
        {
          module: 'word',
          prop: 'code',
          label: '物料编号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'name',
          label: '物料名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'spec',
          label: '规格型号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '评估类型',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'description',
          label: '物料描述',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMeasureName',
          label: '计量单位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'type_dictText',
          label: '物料类型',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'minSkuUnitName',
          label: '最小单位SKU',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'spq',
          label: 'SPQ',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'expirationDate',
          label: '是否有保质期',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (column) => !column.isExpirationDate ? '否' : '是',
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组编号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDescription',
          label: '物料组描述',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources_dictText',
          label: '数据来源',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增${this.title}`
      } else {
        return `编辑${this.title}`
      }
    }
  },
  watch: {
    $route: {
      handler({ params }) {
        if (params.code) {
          this.query.materialCodeOrName = this.$route.params.code
          this.init()
        }
      },
      immediate: true
    }
  },
  mounted() {
    this.init()
  },
  activated() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query,
          materialIdList: []

        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 附件上传成功
    handleUploadSuccess() {
      this.init()
      this.$notify.success({
        title: '成功',
        message: '上传成功'
      })
    },
    // 附件上传失败
    handleUploadFileError() {
      this.$notify.error({
        title: '错误',
        message: '上传失败'
      })
    },
    // 下载模板
    handleDownloadTem() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/basic/wmsMaterial/downloadImportTemplate`)
    },
    // 新增
    handleAdd() {
      this.dialogType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.dialogType = 'modify'
      this.dialogVisible = true
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该${this.title}, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        getDeleteApi({
          id: item.id,
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

