<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="物料编号" prop="code">
          <el-input v-model.trim="form.code" :disabled="editType === 'modify'" placeholder="请输入物料编号" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="物料名称" prop="name">
          <el-input v-model.trim="form.name" placeholder="请输入物料名称" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="规格型号" prop="spec">
          <el-input v-model.trim="form.spec" placeholder="请输入规格型号" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="物料描述" prop="description">
          <el-input v-model.trim="form.description" placeholder="请输入物料描述" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="计量单位" prop="unitMeasureId">
          <el-select
            v-model="form.unitMeasureId"
            filterable
            clearable
            placeholder="请选择计量单位"
            style="width: 178px;"
          >
            <el-option
              v-for="item in unitMeasurePullList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="物料类型" prop="type">
          <el-select
            v-model="form.type"
            filterable
            clearable
            placeholder="请选择物料类型"
            style="width: 178px;"
          >
            <el-option
              v-for="item in dict['wms_material_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="评估类型" prop="assessType">
          <el-select
            v-model="form.assessType"
            filterable
            clearable
            placeholder="请选择评估类型"
            style="width: 178px;"
          >
            <el-option
              v-for="item in dict['wms_material_assess_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="最小包装单位" prop="minSkuUnitId">
          <el-select
            v-model="form.minSkuUnitId"
            filterable
            clearable
            placeholder="请选择最小包装单位"
            style="width: 178px;"
          >
            <el-option
              v-for="item in minSkuUnitPullList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="SPQ" prop="spq">
          <el-input v-model.trim="form.spq" placeholder="请输入SPQ" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="是否有保质期" prop="isExpirationDate">
          <el-radio-group v-model="form.isExpirationDate" style="width: 178px;">
            <el-radio-button
              v-for="item in isEnableOptions"
              :key="item.value"
              :label="item.value"
            >{{ item.label }}</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.isExpirationDate" label="保质期预警天数" prop="earlyWarningDays">
          <el-input-number v-model="form.earlyWarningDays" :min="0" label="保质期预警天数" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="物料组" prop="groupId">
          <el-input v-model.trim="form.materialGroupCode" disabled placeholder="请选择物料组" style="width:calc(100% - 35px)" />
          <el-button type="success" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-plus" @click="materialVisible = true" />
        </el-form-item>
        <el-form-item label="安全库存上限" prop="maxNum">
          <el-input-number v-model="form.maxNum" :min="0" :precision="3" label="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="安全库存下限" prop="minNum">
          <el-input-number v-model="form.minNum" :min="0" :precision="3" label="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="重要等级" prop="importantGrade">
          <el-select
            v-model="form.importantGrade"
            class="filter-item"
            placeholder="重要等级"
            filterable
            clearable
            style="width: 178px;"
          >
            <el-option v-for="(item, index) in dict['wms_material_important_grade']" :key="index" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
        <div />
        <el-form-item label="图片" prop="fileIds" class="wrapper-upload-com">
          <el-upload
            class="avatar-uploader"
            :action="`${BASE_API}${url}`"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
            :disabled="!!uploadFileList.length"
          >
            <div v-if="imageUrl" class="avatar">
              <span class="delPicBtn">
                <span @click="handleRemove(uploadFileList)">删除</span>
              </span>
              <img :src="imageUrl" style="width: 100%;height: 100%;">
            </div>
            <i v-else class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
    <!-- 选择物料组 -->
    <material-information v-model="materialVisible" mode="single" :selected-codes="form.groupId ? [form.groupId] : []" @confirm="onmaterialSelect" />
  </el-drawer>
</template>

<script>
import { getAddApi as addApi, getEditApi as updataApi } from '@/apis/modules/base/baseMaterialInformation'
import MaterialInformation from '@/components/ModalSelect/MaterialInformation'
import { getPullListItemApi as pullListApi } from '@/apis/modules/base/baseUnit'
import { getUploadedFileListCommonApi, deleteUploadedFileCommonApi } from '@/apis/modules/common'
import * as imageConversion from 'image-conversion'
// Tip 混入文件
import QueryItemMixin from '@/mixins/queryItem'
const INIT_FORM = () => ({
  code: '',
  name: '',
  spec: '',
  description: '',
  unitMeasureId: '',
  type: '',
  assessType: '',
  minSkuUnitId: '',
  spq: '',
  isExpirationDate: 0, // 是否有保证期
  earlyWarningDays: 0, // 保质期预警天数
  maxNum: 0, // 库存上限
  minNum: 0, // 库存下限
  groupId: '', // 物料组Id
  materialGroupCode: '', // 物料组名称
  fileIds: '' // 值为附件列表id拼接的字符串
})

export default {
  name: 'CustomerInfoDialogComs',
  components: {
    MaterialInformation
  },
  mixins: [
    QueryItemMixin
  ],
  dicts: ['wms_material_type', 'wms_material_assess_type', 'wms_material_important_grade'],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      materialVisible: false, // 选择物料组
      loading: false,
      // 添加额外的事件
      tableColumnChooseExtraHandle: {},
      isEnableOptions: [{ value: 0, label: '否' }, { value: 1, label: '是' }],
      rules: {
        code: [
          { required: true, message: '请输入物料组编号', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入物料名称', trigger: 'blur' }
        ],
        spec: [
          { required: true, message: '请输入规格型号', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '请输入物料描述', trigger: 'blur' }
        ],
        unitMeasureId: [
          { required: true, message: '请选择计量单位', trigger: 'change' }
        ],
        type: [
          { required: true, message: '请选择物料类型', trigger: 'change' }
        ],
        assessType: [
          { required: true, message: '请选择评估类型', trigger: 'change' }
        ],
        // minSkuUnitId: [
        //   { required: true, message: '请选择最小包装单位', trigger: 'blur' }
        // ],
        // spq: [
        //   { required: true, message: '请输入SPQ', trigger: 'blur' }
        // ],
        isExpirationDate: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        earlyWarningDays: [
          { required: true, message: '请填写', trigger: 'blur' }
        ],
        groupId: [
          { required: true, message: '请选择物料组', trigger: 'change' }
        ]
        // importantGrade: [
        //   { required: true, message: '请选择重要等级', trigger: 'change' }
        // ]
        // fileIds: [
        //   { required: true, message: '请上传图片', trigger: 'change' }
        // ]
      },
      gradeOpts: [],
      // 关于查询项下拉
      pullListConfig: [
        { api: pullListApi({ type: 1 }), dataKey: 'unitMeasurePullList', name: '计量单位' },
        { api: pullListApi({ type: 2 }), dataKey: 'minSkuUnitPullList', name: '最小单位SKU' }
      ],
      unitMeasurePullList: [],
      minSkuUnitPullList: [],
      BASE_API: process.env.VUE_APP_BASE_API_WMS,
      uploadFileList: [],
      url: '/wms/file/upload',
      imageUrl: ''
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
          this.form.fileIds && this.initUploadFileList(this.form.fileIds.split(','))
        }
      },
      immediate: true
    },
    uploadFileList: {
      handler(val) {
        this.imageUrl = val[0] ? val[0].url : ''
        this.form.fileIds = val.map(item => item.id).join()
      },
      immediate: true
    }
  },
  mounted() {
    this.getPullList() // 下拉
  },
  methods: {
    // 页面下拉数据
    getPullList() {
      const apis = this.pullListConfig.map(c => c.api)
      Promise.all(apis)
        .then(result => {
          if (result.length) {
            result.forEach((c, i) => {
              const dataKey = this.pullListConfig[i].dataKey
              this[dataKey] = !c.code ? c.data : []
            })
          }
        })
        .catch(reason => {
          console.log(reason)
        })
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.form.minNum > this.form.maxNum) {
            this.$message({
              message: `库存下限不能超过上限`,
              type: 'warning'
            })
            return
          }
          this.loading = true
          const api = {
            add: addApi,
            modify: updataApi
          }[this.editType]
          api({
            ...this.form
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.form = INIT_FORM()
      this.uploadFileList = []
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // 选择物料组
    onmaterialSelect(data) {
      console.log('data-------------1', data)
      const item = data[0]
      this.form.materialGroupCode = item.code
      this.form.groupId = item.id
    },
    // 反选的时候获取默认的文件列表
    initUploadFileList(fileIdList) {
      if (!fileIdList || !fileIdList.length) return
      return new Promise((resolve, reject) => {
        getUploadedFileListCommonApi({
          ids: fileIdList
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.uploadFileList = data.map(c => ({
              ...c,
              fileId: c.id,
              name: c.fileName,
              url: c.webUrl || `${this.BASE_API}${c.downloadUrl}`
            }))
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // Tip: 文件列表移除文件时的钩子
    handleRemove(fileList) {
      if (!fileList.length) return
      this.$confirm('此操作将永久删除该文件信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteUploadedFileCommonApi({
          id: fileList[0].id
        })
          .then(res => {
            const { code } = res
            if (!code) {
              this.uploadFileList = []
              this.$notify.success({
                title: '成功',
                message: '删除成功'
              })
            }
          }).catch(err => {
            console.log('err', err)
          })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAvatarSuccess(res, file) {
      const { code, data } = res
      if (!code) {
        const file = {
          ...data,
          fileId: data.id,
          name: data.fileName,
          url: `${this.BASE_API}${data.downloadUrl}`
        }
        this.uploadFileList = [file]
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = ['image/jpeg', 'image/jpg', 'image/png'].includes(file.type)
      const isLt2M = file.size / 1024 / 1024 < 5

      if (!isJPG) {
        this.$notify.warning({
          title: '警告',
          message: '上传图片只能是 JPG或PNG 格式!'
        })
      }
      if (!isLt2M) {
        this.$notify.warning({
          title: '警告',
          message: '上传图片大小不能超过 5MB!'
        })
      }

      return isJPG && isLt2M && new Promise((resolve) => {
        // 压缩到100KB,这里的100就是要压缩的大小,可自定义
        imageConversion.compressAccurately(file, 100).then(res => {
          resolve(res)
        })
      })
    }
  }
}
</script>

<style lang='scss' scoped>
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 655px;
  }
  .drawer-footer{
    text-align: right;
  }
  // ::v-deep .el-upload-list__item.is-success.focusing .el-icon-close-tip {
  //   display: none !important;
  // }

  ::v-deep .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  ::v-deep .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  ::v-deep .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  ::v-deep .avatar {
    width: 178px;
    height: 178px;
    position: relative;
    .delPicBtn {
      width: 100%;
      height: 100%;
      display: none;
      color: #F56C6C;
      background-color: rgba(0, 0, 0, 0.5); /* 半透明背景 */
      position: absolute;
      bottom: 100%;
      top: 100%;
      text-align: center;
      line-height: 178px;
    }
    &:hover .delPicBtn {
      display: block;
      bottom: 0%;
      top: 0%;
    }
  }
</style>

